import React from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import './Header.css';
import { Link } from 'react-router-dom';
class HeaderWhite extends React.Component {
    state = {
        expanded: false
    }
    closeNavbar = () => {
        this.setState({expanded: false})
    }            
    render() {
        return(
            <div className="white">
                <Navbar collapseOnSelect expanded={this.state.expanded} bg='light' expand="md" className = "nav-border" >
                    <Navbar.Toggle onClick={() => this.setState({expanded: !this.state.expanded})} aria-controls="responsive-navbar-nav" />
                    <Navbar.Brand className='heading mr-auto ml-auto'> <Link to = "/me">Kunal Pant KP</Link></Navbar.Brand>
                    <Navbar.Collapse id="responsive-navbar-nav" style={{"z-index": 1}}>
                        <Nav className="ml-auto" varient = 'success' defaultActiveKey="/home">
                            <Link className="nav-link" onClick={this.closeNavbar} to="/me/Resume">Resume</Link>
                            <Link className="nav-link" onClick={this.closeNavbar} to="/me/Projects">Projects</Link>
                            <Link className="nav-link" onClick={this.closeNavbar} to="/me/Certificates">Certificates</Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </div>
        )
    }
}
export default HeaderWhite;