import React from 'react';
import { Container, Row, Col, Nav } from 'react-bootstrap';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import EmailIcon from '@material-ui/icons/Email';

class About extends React.Component {
    render() { 
        return(
            <Container fluid>
                <Row className="justify-content-center">
                    <Col sm={12} lg={8}>
                        <h4>About Me</h4> 
                    </Col>
                </Row>
                
                <Row className="justify-content-center">
                
                    <Col sm={12} lg={8}>
                        <p>Hi, I'm a Software Engineer. I love to work on new technologies, like React.js. I enjoy creating new projects. I do competitive programming when I'm bored ;-) Other than programming, travelling interests me.</p>
                    </Col>
                    <Col sm={12} lg={8}>
                        <p><i>Before you leave, I'd love to tell you than I'll be graduating next year and I'm currently seeking for a full-time Job.</i><br/></p>
                    </Col>
                </Row>
                <div className="social">
                        <p><strong>Connect me on</strong></p>
						<Nav className="justify-content-center">
							<Nav.Link href={`mailto:kunalpant95@gmail.com`} target="_blank">
                                <EmailIcon color="primary" />
                            </Nav.Link>
                            <Nav.Link href={`https://linkedin.com/in/kunal-pant-328577132`} target="_blank">
                                <LinkedInIcon color="primary" />
                            </Nav.Link>
                            <Nav.Link href={`https://github.com/pantkunalkp`} target="_blank">
                                <GitHubIcon color="primary"/>
                            </Nav.Link>
						</Nav>
					</div>
                <hr />
            </Container>
        );
    }
}

export default About;
